/* styles.css */
.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr)); /* Создаем сетку с автоматическим распределением колонок */
  gap: 20px; /* Задаем промежуток между карточками */
}

.product-card {
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 10px;
  text-align: center;
}

.product-image {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
  margin-bottom: 10px;
}

.product-card h3 {
  font-size: 1.2rem;
  margin-bottom: 5px;
}

.product-card p {
  margin-bottom: 10px;
}

.product-card a {
  display: inline-block;
  background-color: #007bff;
  color: #fff;
  padding: 5px 10px;
  border-radius: 5px;
  text-decoration: none;
}

.product-card a:hover {
  background-color: #0056b3;
}
